/* Dropdowns */

/* Style The Dropdown Button */
.dropbtn {
  font-family: inherit;
  font-size: 1em;
  padding-top: 10px;
  padding-bottom: 10px;
  color: inherit;
  background-color: black;
  border: none;
  cursor: pointer;
}

/* The container <div> - needed to position the dropdown content */
.dropdown {
  position: relative;
  display: inline-block;
}

/* Dropdown Content (Hidden by Default) */
.dropdown-content {
  display: none;
  position: absolute;
  background-color: #111111;
  min-width: 160px;
  z-index: 1;
}

/* Links inside the dropdown */
.dropdown-content a {
  color: white;
  padding: 12px 16px;
  text-decoration: none;
  display: block;
}

/* Change color of dropdown links on hover */
.dropdown-content a:hover {
  background-color: #555555
}

/* Show the dropdown menu on hover */
.dropdown:hover .dropdown-content {
  display: block;
}

/* Change the background color of the dropdown button when the dropdown content is shown */
.dropdown:hover .dropbtn {
  background-color: #111111;
}

/* Cast page stylings */

.cast-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}
.cast-box {
  width: 400px;
  border: 1px solid #ccc;
  margin: 10px;
  padding: 10px;
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 150px;
  background-color: #282828;
}
.cast-content {
  display: flex;
  flex-grow: 1;
}
.cast-image-container {
  width: 160px;
  height: 160px;
  position: relative;
  margin-right: 15px;
}
.cast-image {
  position: absolute;
  /* Default positioning */
  top: 0;
  left: 0;
}
.cast-image-transition {
  transition: opacity 0.7s ease;
}
.cast-image-hover {
  position: absolute;
  opacity: 0;
  transition: opacity 0.7s ease;
}
.cast-image-container:hover .cast-image-transition {
  opacity: 0;
}
.cast-image-container:hover .cast-image-hover {
  opacity: 1;
}
.cast-text {
  flex: 1;
}
.cast-name {
  font-weight: bold;
  margin: 0px;
}
.cast-description {
  font-size: 0.9em;
  margin-top: 10px;
}
.cast-description-image {
  max-width: 100%;
  justify-content: center;
  align-items: center;
  height: auto;
  display: flex;
  margin-bottom: 5px;
  margin-left: 10px;
}
.cast-info {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 0.8em;
  font-family: monospace;
  border-top: 1px solid #eee;
  padding-top: 10px;
  height: 20px; /* Set this to your desired height */
  margin-top: auto; /* This pushes the info bar to the bottom */
}
.cast-info-left {
  text-align: left;
}
.cast-info-right {
  text-align: right;
}

.chapter-nav {
  display: flex;
  justify-content: center;
  margin-bottom: 30px;
}
.chapter-link {
  padding: 10px 20px;
  margin: 5px 5px;
  border: 2px solid #ccc;
  background-color: #000000;
  color: #ccc;
  text-decoration: none;
  transition: background-color 0.3s ease, color 0.3s ease;
}
.chapter-link:hover {
  background-color: #e0e0e0;
  color:black;
}
.chapter-link.active {
  background-color: #4CAF50;
  color: white;
  border-color: #45a049;
}


